.Header{
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color:#b9eafc;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 90;
}

.UserPic{
    height: 45px;
    width: 45px;
    -o-object-fit: cover;
       object-fit: cover;
    border-radius: 50%;
}


